body {
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

.content {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
  padding: 100px 20px 20px;
}

.timer {
  color: #fff;
  background-color: #333;
  border-radius: 10px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: bold;
  animation: pulse 1s infinite;
  display: inline-block;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
}

@keyframes pulse {
  0%, 100% {
    background-color: #333;
    transform: scale(1);
  }

  50% {
    background-color: #555;
    transform: scale(1.05);
  }
}

.header {
  width: 100%;
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
}

.header, .footer {
  transition: opacity .5s, visibility .5s;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .1);
}

.hidden {
  opacity: 0;
  display: none;
}

.visible {
  opacity: 1;
  display: block;
}

.rakuten-header, .next-header {
  width: 100%;
  z-index: 1000;
  background-color: inherit;
  transition: opacity .5s, visibility .5s;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .1);
}

.rakuten-header {
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 10px 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

.top-section {
  align-items: center;
  gap: 10px;
  display: flex;
}

.top-section .logo {
