  font-size: 24px;
  font-weight: bold;
}

.search-bar {
  flex-grow: 1;
  align-items: center;
  margin: 0 20px;
  display: flex;
}

.search-bar input {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px 0 0 5px;
  padding: 8px;
  font-size: 14px;
}

.search-bar button {
  cursor: pointer;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 0 5px 5px 0;
  padding: 8px;
}

.bottom-section {
  flex-wrap: wrap;
